import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'examType'
})
export class ExamTypePipe implements PipeTransform {

  transform(value: string): any {
    switch (value) {
      case 'GENERAL':
        return '';
      case 'HEALTH_CARE':
        return '';
      case 'embaexam':
        return '';
      case 'phdexam':
        return '';
    }
  }

}



